		display: none !important;
}
}
.home {
	position: fixed;
	box-sizing: border-box;
	top: 10%;
	bottom: 10%;
	left: 15%;
	right: 15%;
	height: auto;
	padding-top: 15px;
	padding-left: 15px;
	padding-right: 15px;
	background-color: #fdfdfd;
	border-radius: 25px;
	box-shadow: 0 5px 10px rgba(0, 0, 0, 0.25);
	overflow-y: hidden;
	z-index: 998;
}
.home_tracing {
	width: 100%;
	height: calc(100% - 50px);
	box-sizing: border-box;
	border-radius: 15px;
}
.home_egg {
	box-sizing: border-box;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
	background-color: #fdfdfd;
	position: relative;
	width: 100%;
	height: 100%;
	border-radius: 15px;
	overflow-y: auto;
	padding: 15px;
}
.home_egg_botom {
	position: relative;
	height: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	align-self: flex-end;
}
.home_egg_botom_button {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 40px;
	height: 40px;
	border: 2px solid #b6c1c7;
	border-radius: 50%;
}
.home_egg_botom_button:hover {
	background-color: #A2B0B8;
}
.home_egg_botom_button:hover .home_egg_botom_button_square {
	border: 1px solid #FFFFFF;
	background-color: #FFFFFF;
}
.home_egg_botom_button:active {
	background-color: #ffffff;
}
.home_egg_botom_button:active .home_egg_botom_button_square {
	border: 1px solid #666666;
	background-color: #666666;
}
.home_egg_botom_button_square {
	border-radius: 3px;
	height: 70%;
	width: 70%;
	background-color: #b6c1c7;
}
@keyframes audioVlog {
from {
		transform: rotate(-30deg);
}
to {
		transform: rotate(30deg);
}
}
@keyframes audioIn {
from {
		transform: rotate(0deg);
}
to {
		transform: rotate(360deg);
}
}

