.fensug {
	width: 9.25rem;
	height: 9.25rem;
	background: #fff;
	border: 9px #5576ac solid;
	margin: 100px auto;
	padding: 5px;
	-webkit-border-radius: 25em;
	-moz-border-radius: 25em;
	border-radius: 25em;
	overflow: hidden;
	position: relative
}
.fensug .wavenum {
	width: 9.25rem;
	height: 9.25rem;
	overflow: hidden;
	-webkit-border-radius: 50%;
	border-radius: 50%;
	text-align: center;
	display: table-cell;
	vertical-align: middle;
	position: absolute;
	left: -10px;
	top: -5px;
	z-index: 6;
