	align-items: center;
	justify-content: center;
	flex-direction: column;
}
.fensug .wavenum b {
	color: #fff;
	font-size: 30px;
	text-align: center;
	display: block;
	position: relative;
	z-index: 2;
	line-height: 45px;
}
.fensug .wavenum tt {
	color: #fff;
	font-size: 16px;
	text-align: center;
	display: block;
	position: relative;
	z-index: 2;
	font-weight: bold;
	width: 124px;
	border-top: 2px #fff solid;
	margin: 3px auto;
	line-height: 35px;
}
.waven {
	/* width: 168px; */
	height: 7.5rem;
	border-radius: 25em;
	background-color: #5576ac;
	overflow: hidden;
	position: relative;
	z-index: 5;
}
.wave {
	width: 25.5rem;
	height: 80%;
	position: absolute;
	left: 0px;
	bottom: 0px;
	z-index: 4;
	background: url(/space/dist/static/img/wave.739f04e.png) no-repeat;
	animation: move_wave 1s linear infinite;
	-webkit-animation: move_wave 1s linear infinite;
}
@keyframes move_wave {
0% {
		transform: translateX(0)
}
50% {
		transform: translateX(-25%)
}
100% {
		transform: translateX(-50%)
}
}

